@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


/* My Styles */

/* General styles and resets */

* {
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    color: rgba(50,50,50,0.9);
    margin: 0;
    text-align: center;
    font-family: 'Nunito', sans-serif;
}

ul {
  list-style: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}


/* Header Styles */

.header {
    padding: 25px;
    border-bottom: 1px solid rgba(50,50,50,0.9);
    box-shadow: 0 2px 3px rgba(50,50,50,0.3);
    background: rgb(100,249,255); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(100,249,255,1) 1%,rgba(0,191,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#64f9ff', endColorstr='#00bfff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  -webkit-filter: saturate(1) drop-shadow(1px 1px 2px rgba(100,100,100,0.5));
          filter: saturate(1) drop-shadow(1px 1px 2px rgba(100,100,100,0.5));
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
  25% { -webkit-transform: rotate(90deg); transform: rotate(90deg);}
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
  75% { -webkit-transform: rotate(270deg); transform: rotate(270deg);}
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
}

@keyframes App-logo-spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
  25% { -webkit-transform: rotate(90deg); transform: rotate(90deg);}
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
  75% { -webkit-transform: rotate(270deg); transform: rotate(270deg);}
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
}

.header h1 {
    color: rgba(250,250,250,0.9);
    text-shadow: 1px 1px 1px rgba(50,50,50,0.7)
}

.header nav {
    width: 450px;
    margin: auto;
    padding-right: 145px;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

.header nav li {
    display: inline-block;
    width: 125px;
    margin: auto 10px;
    background: white;
    border-radius: 5px;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

.header nav li a {
    color: rgba(255,255,255,0.9);
    display: block;
    width: 100%;
    height: 100%;
    padding: 8px 10px;
    background: rgba(0,0,0,1);
    border: 1px solid black;
    border-radius: 4px;
    border: 2px solid rgba(8,12,255,0.7);
    box-shadow: 3px 3px 5px rgba(50,50,50,0.9);
    text-shadow: none;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

.header .dropdown-navmenu {
    position: absolute;
    overflow: hidden;
    cursor: pointer;
    display: inline-block;
    height: 43px;
    padding: 8px 8px;
    color: rgba(255,255,255,0.9);
    background: rgba(0,0,0,1);
    border-radius: 4px;
    border: 2px solid rgba(8,12,255,0.7);
    box-shadow: 3px 3px 5px rgba(50,50,50,0.9);
    text-shadow: none;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

.header .dropdown-navlink {
    margin: 12px auto;
    width: 105px;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
    border: 1px solid rgba(0,191,255,1);
    box-shadow: none;
}

.header .dropdown-navmenu:hover {
    height: 250px;
    color: rgba(0,102,255,0.959);
    background: white;
    box-shadow: 7px 7px 5px rgba(50,50,50,0.4), 2px 2px 9px rgba(0,102,255,0.959) inset, -2px -2px 9px rgba(0,102,255,0.959) inset;
    
}

.header nav li a:hover {
    color: rgba(0,102,255,0.959);
    background: white;
    box-shadow: 7px 7px 5px rgba(50,50,50,0.4), 2px 2px 9px rgba(0,102,255,0.959) inset, -2px -2px 9px rgba(0,102,255,0.959) inset;
    
}

.header .active {
    color: rgba(0,102,255,0.959);
    text-shadow: 1px 1px 0px rgba(8,12,255,0.7);
    box-shadow: 5px 5px 7px rgba(50,50,50,0.4), 2px 2px 9px rgba(0,102,255,0.959) inset, -2px -2px 9px rgba(0,102,255,0.959) inset;
    
}

.header .activeToggle {
    color: rgba(0,102,255,0.959);
    text-shadow: 1px 1px 0px rgba(8,12,255,0.7);
    box-shadow: 5px 5px 7px rgba(50,50,50,0.4), 2px 2px 9px rgba(0,102,255,0.959) inset, -2px -2px 9px rgba(0,102,255,0.959) inset;
    
}


/* Form styles */

form {
    width: 290px;
    margin: 25px auto;
}

input {
    outline: none;
    display: inline-block;
    width: 250px;
    height: 40px;
    padding: 7px;
    border-radius: 4px 0 0 4px;
    border: 1px solid rgba(50,50,50,0.3);
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

input:active, input:focus, input:hover {
    border: 1px solid deepskyblue;
}

form:hover input {
    border: 1px solid deepskyblue;
}

button {
    cursor: pointer;
    display: inline-block;
    height: 40px;
    margin-left: -5px;
    border-radius: 0 4px 4px 0;
    border: 1px solid rgba(50,50,50,0.3);
    border-top-width: 0;
    border-bottom-width: 2px;
    border-right-width: 2px;
    border-left-color: rgba(155,155,155,0.7);
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

form:hover button {
    background: rgba(0,191,255,0.9);
}


/* gallery styles */

.gallery {
    width: 100%;
    max-width: 1400px;
    margin: auto;
}

.gallery h2 {
    margin: 20px auto;
}

.gallery li {
    display: inline-block;
    width: 290px;
    height: 240px;
    margin: 10px;
    background: rgba(0,0,0,1);
    border: 3px solid rgba(50,50,50,0.9);
    border-radius: 3px;
    box-shadow: 3px 3px 5px rgba(50,50,50,0.5);
    overflow: hidden;
    -webkit-transition: ease-out 0.4s;
    transition: ease-out 0.4s;
}

.gallery li:hover {
    box-shadow: 7px 7px 7px rgba(50,50,50,0.5)
}

.gallery li:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.gallery img {
    display: inline-block;;
    width: 100%;
    height: auto;
    vertical-align: middle;
    -webkit-transition: ease-out 0.6s;
    transition: ease-out 0.6s;
}

.loadingClass {
    -webkit-animation: 'fadeOut 0.4s ease-out 0.1s forwards';
            animation: 'fadeOut 0.4s ease-out 0.1s forwards';
}

@-webkit-keyframes fadeOut {
    0% {opacity: 1}
    100% {opacity: 0}
}

@keyframes fadeOut {
    0% {opacity: 1}
    100% {opacity: 0}
}

#comingSoon {
    border: none;
    height: 290px;
    box-shadow: none;
    border-radius: 4px;
}

#searchSpan, #coolStuffSpan {
    cursor: default;
    font-weight: 400;
    font-size: 0.8em;
    color: white;
    background: black;
    margin: auto 3px;
    padding: 3px 8px;
    border-radius: 3px;
    border: 2px solid rgba(8,12,255,0.7);
    box-shadow: 3px 3px 5px rgba(50,50,50,0.9);
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

#searchSpan:hover, #coolStuffSpan:hover {
    color: rgba(0,102,255,0.959);
    text-shadow: 1px 1px 0px rgba(8,12,255,0.7);
    box-shadow: 5px 5px 5px rgba(50,50,50,0.4);
}


/* Footer styles */

.footer {
    color: rgba(255,255,255,0.9);
    padding: 25px;
    margin-top: 100px;
    border-top: 1px solid rgba(50,50,50,0.9);
    box-shadow: 0 2px 3px rgba(50,50,50,0.3) inset;
    background: rgb(150,150,150); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(150,150,150,1) 1%,rgba(50,50,50,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#969696', endColorstr='#323232',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.footer p {
    display: inline-block;
    width: 475px;
    margin: auto;
}

.bonusImg {
    display: inline-block;
    height: 80px;

}

